* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

body {
    background-color: #fafafa;
    /* position: relative; */
    width: 100%;
}

.App {
    padding: 30px;
}




h1 {
    font-weight: 600;
    font-size: 40px;
}

@media screen and (min-width:870px) {
    body {
        display: flex;
        justify-content: center;
    }

    .App {
        width: 700px;
    }

    h1,
    h2 {
        text-align: center;
    }

    .newtodo-btn {
        position:sticky;
        width: 100%;
        margin-top: 10px;
    }
}